/* body and footer css */
html,
body{
  margin: 0;
}
#root{
  height: 100vh;
  width: 100vw;
  margin: 0;
}
* {
  box-sizing: border-box;
}

/* footer */

.footer {
  padding: 0.5em;
  background: #f0f0f0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  }
  
  .footer-right h3 {
  font-family: Mallanna;
  font-weight: 100;
  font-size: 1.2em;
  text-transform: capitalize;
  letter-spacing: 0.02em;
  font-weight: 900;
  }
  
  .footer-icons {
  display: flex;
  gap: 20px;
  }
  
  @media (max-width: 768px) {
  .footer {
  flex-direction: column;
  align-items: center;
  }
  .footer-icons {
  flex-wrap: wrap;
  gap: 10px;
  }
  }
/* header css */
.header-first img {
  height: 90px;
}

@media (max-width: 600px) {
  .header-first img {
    height: 60px;
  }
  .footer-right h3 {
    font-size: 1em;
  }
}
.header {
  display: flex;
  padding: 15px 50px;
  justify-content: space-between;
  position: sticky;
  background-color: #f0f0f0;
  z-index: 300;
  top: 0px;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 0.75);
}
@media only screen and (max-width: 768px) {
  .header {
    padding: 10px 20px;
  }
}
.header-first {
  display: flex;
  justify-content: space-evenly;
}
.instagram .MuiSvgIcon-root {
  background: radial-gradient(
      circle farthest-corner at 35% 90%,
      #fec564,
      transparent 50%
    ),
    radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%),
    radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%),
    radial-gradient(
      ellipse farthest-corner at 20% -50%,
      #5258cf,
      transparent 50%
    ),
    radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%),
    radial-gradient(
      ellipse farthest-corner at 60% -20%,
      #893dc2,
      transparent 50%
    ),
    radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent),
    linear-gradient(
      #6559ca,
      #bc318f 30%,
      #e33f5f 50%,
      #f77638 70%,
      #fec66d 100%
    );
  border-radius: 10px;
  color: #6e1475;
}
.linkedin .MuiSvgIcon-root {
  color: rgb(18, 73, 145);
}
.github .MuiSvgIcon-root {
  color: #212020;
}
.header-middle {
  display: flex;
  flex: 1;
  justify-content: center;
}

.header-option > .MuiSvgIcon-root {
  color: grey;
}

.header-option:hover .MuiSvgIcon-root {
  color: rgba(37, 139, 255, 0.973);
}

.header-option {
  display: flex;
  align-items: center;
  padding: 20px 50px;
  cursor: pointer;
}

.header-option:hover {
  background-color: rgb(214, 209, 204);
  border-radius: 20px;
  align-items: center;
  border-bottom: none;
  padding: 20px 50px;
}

.header-info {
  display: flex;
  align-items: center;
}

.header-info h4 {
  margin-left: 10px;
}

.header-last {
  display: flex;
}

@media (max-width: 600px) {
  .header-middle {
    flex-wrap: wrap;
  }
  .header-option {
    padding: 10px;
  }
  .header-info h4 {
    font-size: 0.8em;
  }
}
/*create formcss */
.create-box-wrapper{
  background-color: #e3d9ea;
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
.create-box {
  width: auto;
  height: auto;
  padding: 50px;
  display: flex;
  flex-direction: column;
  background: rgb(238, 236, 236);
  border-radius: 10px;
  gap: 30px;
  justify-content: center;
  /* align-items: center; */
  box-shadow: 0px 0px 10px 0px black;
}
.form-data-create {
  display: flex;
  width:auto;
  height:auto;
  max-width:100%;
  max-height:100%;
  align-items: center;
  max-width: auto;
  overflow: visible;
  gap: 25px;
  flex-direction: column;
}
.form-data-create input {
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgb(105, 98, 197);
  background-color: rgb(239, 239, 239);
  border: none;
  width: 250px;
  height: 25px;
  padding: 5px 0;
  outline: none;
}

@media (max-width: 600px) {
  .create-box {
   width: auto;
  }
}
.form-data-create select {
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgb(105, 98, 197);
  background-color: rgb(239, 239, 239);
  border: none;
  width: 250px;
  height: 30px;
  padding: 5px 0;
  outline: none;
}
.form-data-create input:hover {
  padding: 5px 0;
  border-radius: 5px;
  border: 2px solid rgba(104, 85, 224, 0.2);
}
.text-area textarea {
  border-radius: 10px;
  width: 350px;
  height: 150px;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 1px solid rgb(105, 98, 197);
  font-size: 15px;
  resize: none;
}
.text-area textarea::-webkit-scrollbar {
  width: 12px;
  background-color: #f7ebf6;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

@media (max-width: 600px) {
  .form-data-create select {
    width: 100%;
  }
  .text-area textarea {
    width: 100%;
  }
}

.text-area textarea::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgba(104, 85, 224, 1);
}
.form-data-create button:hover {
  color: white;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.6);
  background-color: rgba(104, 85, 224, 1);
}
/* login signup form */

.container {
  display: flex;
  flex-wrap: wrap; 
  height: 100%;
  width: auto;
}
.overlay-container {
  background-color: #9e8d91;
  display: flex;
  flex: 1.3;
  justify-content: center;
  align-items: center;
  background: -webkit-linear-gradient(to right, #69788f, #4a5e7d);
  background: linear-gradient(to right, #5f6984, #21345b);
}
.overlay-panel {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  height: 100%;
  width: 50%;
}

@media (max-width: 870px) {
  .overlay-container{
    display: none;
  }
  .form-data{
    box-shadow: 0px 0px 10px 0px rgb(105, 98, 197);
    padding: 50px;
    border-radius: 10px;
  }

}
.errors{
  color: red;
}
.login-wrapper {
  display: flex;
  
  justify-content: space-between;
}

.form-data {
  display: flex;
  align-items: center;
  flex-direction: column;
  row-gap: 40px;
}
.login-box {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
}
.form-data input {
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgb(105, 98, 197);
  background-color: rgb(239, 239, 239);
  border: none;
  width: 190px;
  padding: 5px 0;
  outline: none;
}

@media (max-width: 600px) {
  .form-data input {
    width: 80%;
  }
}

.form-data input:hover {
  padding: 5px 0;
  border-radius: 5px;
  border: 2px solid rgba(104, 85, 224, 0.2);
}
.form-data .log {
  cursor: pointer;
  border-radius: 4px;
  font-weight: 600;
  width: 200px;
  padding: 10px 0;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.2);
  transition: 0.4s;
  color: rgb(104, 85, 224);
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid rgba(104, 85, 224, 1);
}
.form-data button:hover {
  color: white;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.6);
  background-color: rgba(104, 85, 224, 1);
}

.signup-button {
  cursor: pointer;
  border: 0;
  border-radius: 4px;
  font-weight: 600;
  width: 200px;
  padding: 10px 0;
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.2);
  transition: 0.4s;
  color: white;
  background-color: rgba(104, 85, 224, 1);
}

@media (max-width: 700px) {
  .form-data .log,
  .signup-button {
    width: 80%;
  }
}

.login-box img {
  height: 150px;
  width: auto;
}
.login-errors {
  color: rgb(239, 30, 30);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 0px;
}

@media (max-width: 600px) {
  .login-box img {
    height: 80px;
  }
  .login-errors {
    width: 80%;
  }
}

/* postcss */

.search-bar {
  background: rgba(115, 97, 160, 0.2);
  display: flex;
  align-items: center;
  border-radius: 15px;
  width: 100%;
  flex-wrap: wrap;
  max-width: 500px;
  padding: 2px 20px;
}
@media (max-width: 600px) {
  .search-bar {
    max-width: 85%;
  }
}

.search-bar:hover {
  box-shadow: 0 0 20px rgba(104, 85, 224, 0.6);
}
.search-bar input {
  background-color: transparent;
  flex: 1;
  border: 0;
  outline: none;
  padding: 24px 20px;
  font-size: 20px;
  color: #121111;
}
.search-bar input::placeholder {
  color: rgb(43, 146, 197);
}
.search-bar .MuiSvgIcon-root {
  color: rgb(43, 146, 197);
}
.search-bar .MuiSvgIcon-root:hover {
  color: rgb(97, 43, 197);
}

.create-post-wrapper {
  display: flex;
  margin-bottom: 30px;
  padding: 1px 70px;
  align-items: center;
  margin-top: 30px;
  border-radius: 15px;
}
.create-post-wrapper h3 {
  display: flex;
  margin-right: 20px;
}
.route-create {
  background: rgba(115, 97, 160, 0.2);
  display: flex;
  align-items: center;
  border-radius: 15px;
  width: 100%;
  max-width: 500px;
  padding: 2px 20px;
}
@media (max-width: 600px) {
  .create-post-wrapper {
    padding: 10px;
    align-items: center;
    margin-top: 30px;
  }
  .route-create {
    max-width: 55%;
  }
}
.route-create input {
  background-color: transparent;
  flex: 1;
  border: 0;
  outline: none;
  padding: 10px 15px;
  font-size: 15px;
  color: #251f1f;
}

.card-title {
  text-transform: capitalize;
}

/* post cards */

.posts-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 0 40px 0;
}

.read-more {
  background-color: transparent;
  border: none;
  font-weight: 500;
  color: #8e00b9;
  cursor: pointer;
}
.individual-cards {
  border-radius: 5px;
  margin-bottom: 30px;
  display: flex;
  max-width: 40%;
  flex-direction: column;
  box-shadow: 0px 0px 7px 0px rgb(204, 163, 225);
}

.individual-cards img {
   width: 40%;
   margin-top: 15px;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 0px 0px black;
}

@media screen and (max-width: 800px) {
  .individual-cards {
    max-width: 90%;
  }
  
  .individual-cards img {
    width: 90%;
  }
}

/* For screens wider than 768px */
@media screen and (min-width: 799px) {
  .individual-cards {
    max-width: 40%;
  }
  
  .individual-cards img {
    width: 40%;
  }
}

.post-items {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
}
.post-right {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}
.post-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1px 50px;
}
.post-buttons {
  display: flex;
}
.post-buttons button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  padding-right: 15px;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
.fav-button .MuiSvgIcon-root {
  color: rgb(239, 39, 39);
}
.delete-button .MuiSvgIcon-root {
  color: rgb(11, 53, 59);
}

.post-text {
  padding: 1px 20px;
}

.comment-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.comment-input-wrapper {
  background: #ebe8e8;
  display: flex;
  margin-bottom: 30px;
  padding: 5px 20px;
  align-items: center;
  margin-top: 30px;
  border-radius: 30px;
  opacity: 8.5;
}
@media screen and (max-width: 800px) {
  .comment-input-wrapper {
    flex-wrap: wrap;
    max-width: 70%;
  }
}

/* For screens wider than 768px */
@media screen and (min-width: 799px) {
  .comment-input-wrapper {
    /* background-color: #064772; */
    max-width: 80%;
  }
}
.comment-input-wrapper input {
  background-color: transparent;
  flex: 1;
  border: 0;
  outline: none;
  padding: 1px 100px;
  font-size: 15px;
  color: #000000;
}
.comment-input-wrapper input::placeholder {
  color: #5f3939;
}
.comment-input-wrapper button {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
}
.comment-button .MuiSvgIcon-root {
  color: #064772;
}
.comment-button .MuiSvgIcon-root:hover {
  color: #4f32ac;
}

.comment-area {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-height: 150px;
  overflow-y: scroll;
  margin-top: 30px;
  border-radius: 10px;
  background: #f6f6f6;
}

.comment-area::-webkit-scrollbar {
  width: 12px;
  background-color: #f7ebf6;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.comment-area::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(144, 140, 170);
}
.main-comments {
  display: flex;
  align-items: center;
  padding: 10px 40px;
}
.comment-button-wrapper {
  background: #cacaca;
  border-radius: 99px;
  padding-right: 20px;
  display: flex;
  padding-left: 10px;
  column-gap: 1em;
  align-items: center;
  height: 50px;
}
.comment-button-wrapper h4 {
  color: #2f0c5b;
  font-family: Mallanna;
  font-weight: 100;
  font-size: 1.2em;
  text-transform: capitalize;
  letter-spacing: 0.02em;
  font-weight: 900;
}
.comment-delete {
  background-color: Transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
}
.comments-plus {
  display: flex;
}

/* userand done */

.App-user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.user-card {
  display: flex;
  flex-wrap: wrap;
  column-gap: 2em;
  max-width: 100%;
  align-items: center;
  padding: 50px 100px;
}
@media screen and (max-width: 750px) {
  .user-card{
    width: 100%;
  }
}

.image-container img {
  border-radius: 99px;
  width: 150px;
  height: 150px;
}
.right-user {
  display: flex;
  flex-direction: column;
  width: 700px;
}

.username-gear {
  display: flex;
  column-gap: 1em;
  align-items: center;
}
.username-gear button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  padding-right: 15px;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.username-gear .MuiSvgIcon-root {
  color: #064772;
}
.username-gear .MuiSvgIcon-root:hover {
  color: rgb(176, 77, 215);
}

.fav-button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  padding-right: 15px;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
.fav-button .MuiSvgIcon-root {
  color: red;
}

.showcase {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 20px;
}
.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: var(--primary-color) linear-gradient(to right, #e274fb, #9b58bc)
    no-repeat center center/cover;
}
.video-container video {
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.video-container:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.video-content {
  z-index: 10;
  color: #c38f7a;
  background-color: rgba(85, 84, 84, 0.5);
  border-radius: 10px;
  padding: 100px;
}
.video-content a{
  text-decoration: none;
  box-shadow: inset 0 0 0 0 rgb(128, 99, 235);
  color: #c38f7a;
  transition: color .5s ease-in-out, box-shadow .5s ease-in-out;
}

.video-content a:hover{
  color: rgb(128, 99, 235);
  box-shadow: inset 100px 0 0 0 #d1adad;
  border-radius: 5px;
}